const menuStyle = {
  height: '60px',
  color: '#a8a8a8',
  'border-top': '1px solid #dedede',
  'background-color': 'white',
  position: 'fixed',
  bottom: 0
}

export default function Menu(props) {
  return (
    <div className="wrapper" style={menuStyle}>
      <div className="container menubar">
        <div>Case 1</div>
        <div>Case 2</div>
        <div>Case 3</div>
        <div>Case 4</div>
      </div>
      <style jsx>{`
        .menubar {
          align-items: center;
          padding: 0 14px;
        }
      `}</style>
    </div>
  )
}